import React, { Component } from "react";
import { Article } from "../../types/Types";
import ArticleComponent from "../Article/ArticleComponent";
import { Link } from "react-router-dom";

interface Props {
  data: Article[];
  articleInfo?: [];
  loadArticleInfo?: () => void;
}

export default class ArticlesList extends Component<Props> {
  // componentDidMount() {
  //   if (this.props.loadArticleInfo) this.props.loadArticleInfo();
  // }

  render() {
    const { data } = this.props;
    // console.log(data);

    const articlesList = data.map((article, index) => (
      <Link
        to={`/article/${article.name}`}
        key={index}
        className='article-list-item'
        // onClick={loadArticleInfo}
      >
        <ArticleComponent data={article} key={index} />
      </Link>
    ));
    return <>{articlesList}</>;
  }
}
