import React, { Component } from "react";
import { Article } from "../../types/Types";

interface Props {
  data: Article;
  key: number;
}

export default class ArticleComponent extends Component<Props> {
  render() {
    const { data } = this.props;
    // console.log(data);

    return (
      <>
        <h3>{data.title}</h3>
        <p>{data.content[0].substring(0, 150)}... </p>
      </>
    );
  }
}
