import React, { Component } from "react";
import Articles from "../../api/Articles";
import { Article } from "../../types/Types";

interface Props {
  article: Article;
  setArticleInfo: () => void;
}

interface State {
  username: string;
  comment: string;
}

export default class AddCommentForm extends Component<Props> {
  state: State = {
    username: "",
    comment: "",
  };

  handleChange = (e: any) => {
    const { value, name } = e.target;

    this.setState({
      ...this.state,
      [name]: value,
    });
  };

  resetFields = () => {
    this.setState({
      username: "",
      comment: "",
    });
  };

  addComment = async () => {
    const { article } = this.props;
    const { username, comment } = this.state;
    try {
      const data = await Articles.addComment(article, username, comment);
      console.log(data);

      if (data.data !== undefined) {
        this.setState({
          comment: data.data.comments,
        });
      }
      this.props.setArticleInfo();
      this.resetFields();
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const { username, comment } = this.state;
    return (
      <div id='add-comment-form'>
        <h3>Add a Comment</h3>
        <label htmlFor='name'>
          Name:{" "}
          <input
            type='text'
            value={username}
            name='username'
            onChange={this.handleChange}
          />
        </label>
        <label htmlFor='comment'>
          Comment:{" "}
          <textarea
            rows={4}
            cols={50}
            value={comment}
            name='comment'
            onChange={this.handleChange}
          />
        </label>
        <button onClick={this.addComment}>Add Comment</button>
      </div>
    );
  }
}
