import React, { Component } from "react";
import Articles from "../../api/Articles";
import { Article } from "../../types/Types";

interface Props {
  article: Article;
  upvotes: number | undefined;
  loadArticleInfo: () => void;
}

export default class UpvotesSection extends Component<Props> {
  upvoteArticle = async () => {
    const { article } = this.props;
    try {
      const data = await Articles.upvoteArticle(article.name);

      if (data.data !== undefined) {
        this.setState({
          upvotes: data.data.upvotes,
        });
      }
      this.props.loadArticleInfo();
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const { upvotes } = this.props;
    return (
      <div id='upvotes-section'>
        <button onClick={this.upvoteArticle}>Add Upvote</button>
        <p>This post has been upvoted {upvotes} times</p>
      </div>
    );
  }
}
