import React, { Component } from "react";
import ArticlesList from "../../components/ArticlesList/ArticlesList";
import { Article } from "../../types/Types";
import Articles from "../../api/BlogData";

interface State {
  articles: Article[];
  loading: boolean;
}

export default class ArticlesListScreen extends Component<State> {
  state: State = {
    articles: [],
    loading: true,
  };

  loadData = async () => {
    try {
      const data: Article[] = await Articles.getArticles();

      if (data)
        this.setState({
          articles: data,
          loading: false,
        });
    } catch (err) {
      console.log(err);
    }
  };

  componentDidMount() {
    this.loadData();
  }

  render() {
    const { articles } = this.state;
    return (
      <>
        <h1>Articles</h1>
        <ArticlesList data={articles} />
      </>
    );
  }
}
