import React, { Component } from "react";
import { Article } from "../../types/Types";
import Articles from "../../api/BlogData";
import ArticlesList from "../../components/ArticlesList/ArticlesList";
import NotFoundScreen from "../404/NotFoundScreen";
import ArticlesData from "../../api/Articles";
import UpvotesSection from "../../components/UpvotesSection/UpvotesSection";
import CommentsList from "../../components/CommentsList/CommentsList";
import AddCommentForm from "../../components/AddCommentsSection/AddCommentForm";

interface Props {
  match: any;
}

interface State {
  articles: Article[];
  loading: boolean;
  upvotes: number;
  comments: [{ username: string; text: string }];
}

export default class ArticleScreen extends Component<Props, State> {
  state: State = {
    articles: [],
    loading: true,
    upvotes: 0,
    comments: [{ username: "", text: "" }],
  };

  articleName = this.props.match.params;

  loadData = async () => {
    try {
      const data: Article[] = await Articles.getArticles();
      if (data)
        this.setState({
          articles: data,
          loading: false,
        });
      this.loadArticleInfo();
    } catch (err) {
      console.log(err);
    }
  };

  loadArticleInfo = async () => {
    const { name } = this.props.match.params;

    try {
      const data = await ArticlesData.getArticle(name);
      console.log(data);

      if (data.data !== undefined) {
        this.setState(
          {
            upvotes: data.data.upvotes,
            comments: data.data.comments,
          },
          () => console.log(this.state)
        );
      }
    } catch (err) {
      console.log(err);
    }
  };

  componentDidMount() {
    this.loadData();
  }

  componentWillReceiveProps() {
    this.loadData();
  }

  render() {
    const { match } = this.props;
    const { name } = match.params;
    const { articles, upvotes, comments } = this.state;
    console.log(comments);

    const article = articles.find((article) => article.name === name);

    if (!article) return <NotFoundScreen />;

    const otherArticles = articles.filter((article) => article.name !== name);

    return (
      <>
        <h1>{article.title}</h1>
        <UpvotesSection
          article={article}
          upvotes={upvotes}
          loadArticleInfo={this.loadArticleInfo}
        />
        {article.content.map((paragraph, index) => (
          <p key={index}>{paragraph}</p>
        ))}
        <CommentsList comments={comments} />
        <AddCommentForm
          article={article}
          setArticleInfo={this.loadArticleInfo}
        />
        <h2>Other Articles:</h2>
        <ArticlesList data={otherArticles} />
      </>
    );
  }
}
