import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import About from "./pages/About/About";
import ArticleScreen from "./pages/Article/ArticleScreen";
import ArticlesList from "./pages/ArticlesListScreen/ArticlesListScreen";
import Home from "./pages/Home/Home";
import "./App.css";
import NavBar from "./components/NavBar/NavBar";
import NotFoundScreen from "./pages/404/NotFoundScreen";

export default class App extends Component {
  render() {
    return (
      <Router>
        <div className='App'>
          <NavBar />
          <div id='page-body'>
            <Switch>
              <Route path='/' component={Home} exact />
              <Route path='/about' component={About} />
              <Route path='/articles-list' component={ArticlesList} />
              <Route path='/article/:name' component={ArticleScreen} />
              <Route component={NotFoundScreen} />
            </Switch>
          </div>
        </div>
      </Router>
    );
  }
}
