import api from "./Api";
import { ApiResponse } from "apisauce";
import { Article, ArticleInfo } from "../types/Types";

export default class Articles {
  static getArticles = async (): Promise<ApiResponse<ArticleInfo[]>> => {
    return api.get(`/articles`);
  };

  static getArticle = async (
    name: string
  ): Promise<ApiResponse<ArticleInfo>> => {
    return api.get(`/articles/${name}`);
  };

  static upvoteArticle = async (
    name: string
  ): Promise<ApiResponse<ArticleInfo>> => {
    return api.post(`/articles/${name}/upvote`);
  };

  static addComment = async (
    article: Article,
    username: string,
    text: string
  ): Promise<ApiResponse<ArticleInfo>> => {
    console.log(article, username, text);

    return api.post(`/articles/${article.name}/add-comment`, {
      username,
      text,
    });
  };

  static deleteArticle = async (
    name: string
  ): Promise<ApiResponse<ArticleInfo>> => {
    return api.delete(`/articles/${name}`);
  };
}
